<template>
<div>
  <div class="home-title">
    <span>产品管理</span>
  </div>
  <div class="product-info">
    <div class="product-content">
      <a-row>
        <a-col span="14">
          <h3 style="font-weight: bold;">功能介绍</h3>
          <p>在物联网平台中，某一类具有相同能力或特征的设备的合集被称为一款产品。</p>
          <p>如果您希望使用平台查看设备上报的数据信息，并对设备进行管理控制，就需要开发产品模型(Model)。定义Model，使平台理解该款设备支持的属性、命令等信息。根据产品的接入协议、数据格式等可能还需要您定义其他相关的内容</p>
        </a-col>
        <a-col span="10">
          <img src="../../assets/images/product/product.png" height="100%" width="100%"/>
        </a-col>
      </a-row>
    </div>
  </div>
  <div class="table-content">
    <div class="table-title">
      <a-button type="primary" @click="showModal"> <a-icon type="plus"/> 创建产品</a-button>
      <a-input-search allowClear @blur="onSearch" v-model="searchObj.productLabel" @search="onSearch" style="width: 200px; margin: 0 20px" placeholder="请输入产品名称搜索" />
    </div>
    <div class="table-box">
      <a-table :columns="columns"
               @change="tableChange"
               :rowKey="(record, index) => {return index}"
               :pagination="pagination"
               :data-source="data">
        <a slot="name" slot-scope="text">{{ text }}</a>
        <span slot="action" slot-scope="text, record">
        <span class="table-btn" @click="goDetail(record.id)">查看</span>
         <a-popconfirm
           title="你确定删除此产品吗?"
           ok-text="确定"
           cancel-text="取消"
           @confirm="deleteCancel(record.id)"
         >
   <span class="table-btn"> 删除 </span>
  </a-popconfirm>
    </span>
      </a-table>
    </div>
  </div>
  <a-modal v-model="visible" :width="700" title="创建产品" @ok="handleOk" @cancel="colseHandle">
    <a-form-model
      ref="productForm"
      :model="form"
      :rules="rules"
      :confirm-loading="confirmLoading"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-model-item ref="name" label="产品名称" prop="productLabel">
        <a-input v-model="form.productLabel" placeholder="请输入产品名称"/>
      </a-form-model-item>
      <a-form-model-item label="协议类型" prop="protocolType">
        <a-select v-model="form.protocolType" placeholder="请选择协议类型">
          <a-select-option value="MQTT">
            MQTT
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="数据格式" prop="dataFormat">
        <a-select v-model="form.dataFormat" placeholder="请选择数据格式">
          <a-select-option value="JSON">
           JSON
          </a-select-option>
          <a-select-option value="BINARY">
           二进制码流
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="设备类型">
        <a-input v-model="form.deviceType" placeholder="请输入设备类型"/>
      </a-form-model-item>
      <a-form-model-item label="产品描述">
        <BTextarea
          class="full-width"
          style="height: auto"
          v-model="form.description"
          placeholder="请输入描述"
        />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</div>
</template>

<script>
import BTextarea from '../../components/BTextarea/BTextarea'
const columns = [
  {
    dataIndex: 'productLabel',
    title: '产品名称',
    ellipsis: true,
    key: 'productLabel'
  },
  {
    title: '产品ID',
    dataIndex: 'id',
    ellipsis: true,
    key: 'id'
  },
  {
    title: '设备类型',
    ellipsis: true,
    dataIndex: 'deviceType',
    key: 'deviceType'
  },
  {
    title: '协议类型',
    key: 'protocolType',
    dataIndex: 'protocolType',
    ellipsis: true
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  name: 'productList',
  components: {
    BTextarea
  },
  data () {
    return {
      data: [],
      columns,
      searchObj: {
        productLabel: ''
      },
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      visible: false,
      labelCol: {span: 4},
      wrapperCol: {span: 20},
      other: '',
      form: {
        productLabel: '',
        protocolType: undefined,
        dataFormat: '',
        id: '',
        deviceType: '',
        description: ''
      },
      rules: {
        productLabel: [
          { required: true, message: '请输入产品名称', trigger: 'blur' }
        ],
        dataFormat: [{ required: true, message: '请选择数据格式', trigger: 'change' }],
        protocolType: [{ required: true, message: '请输入协议类型', trigger: 'change' }],
        description: [
          { validator: this.$validator.checkRemarkLength, message: '描述最多128字符', trigger: ['blur', 'change'] }
        ]
      },
      confirmLoading: false
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    /**
     * 切换分页
     * */
    tableChange(pagination) {
      this.pagination.pageSize = pagination.pageSize
      this.pagination.current = pagination.current
      this.pagination.total = pagination.total
      this.getList()
    },
    /**
     * 获取产品列表
     * */
    getList() {
      const pageOption = {
        productLabel: this.searchObj.productLabel,
        page: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.$api.getProductList(pageOption).then(res => {
        const resData = res.data
        console.log('数据', res)
        this.pagination.total = resData.total
        this.pagination.pageSize = resData.size
        this.pagination.current = resData.current
        const resRecords = resData.records
        // resRecords.map((item, index) => Object.assign(item, {
        //   key: index + 1,
        //   remark: item.remark ? item.remark : '无',
        //   createTime: this.$getDate({timestamp: item.createTime})
        // }))
        this.data = resRecords
      })
    },
    /**
     * 删除产品
     * */
    deleteCancel(id) {
      this.$api.deleteProduct(id).then(res => {
        if (res.data) {
          this.getList()
        }
      })
    },
    showModal() {
      this.visible = true
      this.form = {}
    },
    handleOk() {
      this.confirmLoading = true
      this.$refs.productForm.validate(valid => {
        if (valid) {
          this.$api.postProductAdd(this.form).then(res => {
            this.visible = false
            this.confirmLoading = false
            this.getList()
          })
        }
      })
    },
    colseHandle() {
      this.visible = false
      this.$refs['productForm'].resetFields()
    },
    onSearch() {
      this.getList()
    },
    goDetail (id) {
      console.log('id', id)
      this.$router.push({
        path: '/productDetails',
        query: {
          id: id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.product-info {
  background: #ffffff;
  margin: 24px;
  .product-content {
    padding: 24px;
    color: #8a8e99;
    font-size: 16px;
  }
}
</style>



